<template>
  <div class="goods_info">
    <img class="goods-img" src="../../assets/person/banner01.jpg"/>
    <div class="infor">
    <div class="name">的好时机开发商的交付更待何时奉公守法附近的国防生的</div>
    <div class="flex-item">
    	<div class="price">￥200.00</div>
    	<div class="sales">销量 266</div>
    </div>
    </div>
    
    <div id="attr-box">
    	<div class="flex-item">
    		<div>
    			<span class="l-text">已选</span><span>正红色 1个</span>
    		</div>
    		<span>></span>
    	</div>
		<div class="flex-wrap">
			<div class="l-text">颜色</div>
			<div>
				<span class="optionb">正红色</span>
				<span class="optionb">珊瑚色</span>
				<span class="optionb">橘红色</span>
			</div>
		</div>
		<div class="flex-wrap quantity">
			<div class="l-text">数量</div>
		
			<div>
				<span class="minus" id="min">
					<!--<img src="img/img-icons/icon-min.png" style="width: 12px;"/>-->-
				</span>
				<input class="num" id="quantity" name="" type="text" value="1" disabled />
				<span class="plus" id="add">
					<!--<img src="img/img-icons/icon-add.png" style="width: 12px;"/>-->+
				</span>
			</div>
		</div>
	</div>
	<div class="details">
		<div class="center-text">商品介绍</div>
		<img class="goods-img" src="../../assets/person/banner01.jpg"/>
	</div>
	
	<div class="fixed-bottom">
		<div><img src="../../assets/person/banner01.jpg" style="width: .4rem;height: .4rem;"/></div>
		<div>加入购物车</div>
		<div @click="confirm">立即购买</div>
		
	</div>
  </div>
</template>
<script>
  import { Toast} from 'mint-ui';
  export default {
    data(){
      return {
        flag: false,
        goodsInfo: {},
        items: [
        {src:'../../assets/shopImage/cut.png'},
        {src:'../../assets/shopImage/cut.png'}
        ],
        num:1,
        //商品id
        id: '',
        type:1
      }
    },
    
    methods:{
    	confirm(){
    		this.$router.push('/confirm')
    	},
    	getShopById(good_id){
     	this.post('home/Category/getShopById', {id:good_id}).then(function(res) {
     		console.log(res)
//        if (res.data.code === 10000) {
//         
//        }
        }.bind(this))
     }
    },
    mounted(){
     this.getShopById(this.$route.params.id)
    }
  }
</script>
<style scoped>
  @import '../../../static/header.css';
  .infor{background: #fff;padding: .2rem;}
  .flex-item{display: flex;flex-direction: row;justify-content: space-between;align-items: center;}
  .goods_info {
    height: calc(100vh - 0.9rem);
    overflow-y: auto;
  }
  .goods-img{display: block;width: 100%;}
  
  
#attr-box {background: #fff;padding:0 .2rem .2rem;border: 1px rgba(0,0,0,0) solid;margin: .2rem 0;}
#attr-box .flex-wrap{display: flex;margin-top: 10px;flex-direction: row;align-items: center;}

.l-text{height: 36px;line-height: 36px;color: #A2A3A3;margin-right: .4rem;font-size: .26rem;}
.optiona,.optionb{width:70px;height:36px;line-height:36px;text-align:center;border-radius: 6px;background: #F2F2F4;display: inline-block;margin-right: 10px;}
.option-active{background: #62ad44;color: #fff;}
.quantity span,.quantity input{width: 36px;text-align: center;background: #F2F2F4;border-radius: 3px;display: inline-block;height: .6rem;line-height: .6rem;}
.quantity input{width: 70px;border: 0;outline: none;}


.details .center-text{text-align: center;padding: .2rem;background: #fff;}
.fixed-bottom{position: fixed;bottom:0;max-width: 750px;margin: auto;color: #fff;
 width: 100%;height: 1rem;line-height: 1rem; background:#fff ;text-align: center;display: flex;align-items: center;}
 
 .fixed-bottom div{
 	flex: 1;
 }
 .fixed-bottom div:nth-of-type(2){background: #FF798F;}
 .fixed-bottom div:nth-of-type(3){background: #FF5773;}
</style>



